<template>
    <div class="myBody">
<div style="height:25%;"></div>
      <Card style="width: 500px; margin:0 auto;">
        <p slot="title">系统登陆</p>
        <div>

          <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
            <FormItem prop="user">
              用户名：
              <Input type="text" v-model="formInline.user" placeholder="请输入用户名">
                <Icon type="ios-person-outline" slot="prepend"></Icon>
              </Input>
            </FormItem>
            <FormItem prop="password">
              密　码：
              <Input type="password" v-model="formInline.password" placeholder="请输入密码">
                <Icon type="ios-lock-outline" slot="prepend"></Icon>
              </Input>
            </FormItem>
            <FormItem>
              <Button type="primary" @click="handleSubmit('formInline')">登录</Button>
            </FormItem>
          </Form>

        </div>
      </Card>

    </div>
</template>

<script>
    export default {
        name: "login",
      data () {
        return {
          formInline: {
            user: 'admin',
            password: 'admin888'
          },
          ruleInline: {
            user: [
              { required: true, message: '用户名不能为空', trigger: 'blur' }
            ],
            password: [
              { required: true, message: '密码不能为空', trigger: 'blur' },
              { type: 'string', min: 6, message: '密码不能少于6位', trigger: 'blur' }
            ]
          }
        }
      },
      methods: {
        handleSubmit(name) {
          this.$refs[name].validate((valid) => {
            if (valid) {

              console.info(valid)
              console.info(this.formInline.user)
              console.info(this.formInline.password)

              if(this.formInline.user == "admin" && this.formInline.password == "admin888")
              {
                this.$Message.success('提交成功!')

                sessionStorage.setItem("isLogin","ok")

                this.$store.commit('isLogin',"ok")

                console.info(this.$store.state.isLoginState)
                this.$router.push({ path: '/main' })
                //console.info(this.$store.isLogin)
                //console.info(response.body)
              }
              else
              {
                this.$Message.error('账号或密码错误，请从新提交!');
              }

            } else {
              this.$Message.error('数据有误，请从新提交!');
            }
          })
        }
      }
    }
</script>

<style scoped>
.myBody{ background-color: #eeeeee; height:100vh;}
  .inputClass { padding:10px;}
  .ivu-form-item { display:block !important;}
</style>



